import { useEffect, useState, Fragment, useCallback } from "react";
import "./index.css";
import { useSelector } from "react-redux";
import Axios from "axios";

let photos = [];

const DetailsAds = () => {

  const ads_id = localStorage.getItem("ads_id");

  const [adsInfo, setAdsInfo] = useState({});

  const BASE_API_URL = useSelector((state) => state.BASE_API_URL);

  const getAdsDetails = useCallback(() => {
    return new Promise((resolve, reject) => {
      Axios.get(`${BASE_API_URL}/api/ads/ads-info/${ads_id}`)
        .then((res) => {
          resolve(res.data);
        })
        .catch((err) => reject(err));
    });
  }, [BASE_API_URL, ads_id]);
  useEffect(() => {
    getAdsDetails()
      .then((data) => {
        setAdsInfo(data);
        photos.push(data.file_paths)
        for(let i=1 ; i<= photos.length+1 ; i++){
          console.log(photos[0][i])
        }
      })
      .catch((err) => console.log(err));
  }, [BASE_API_URL, getAdsDetails]);

  return (
    <div className="details_ads">
      <div className="rectangle">
        {<Fragment key={adsInfo._id}>
            <DisplyPhotos/>
            <p>{adsInfo.text}</p>
          </Fragment>}
      </div>
    </div>
  );
};

export default DetailsAds;


const DisplyPhotos = () => {
  const BASE_API_URL = useSelector((state) => state.BASE_API_URL);
  return(
    <div>
        {(() => {
            let td = [];
            for (let i = 1; i <= photos.length; i++) {
              td.push(
                <div key={i}>
                  <img src={`${BASE_API_URL}/${photos[0][i]}`} alt="" width="100px"/>
                </div>
              );
            }
            return td;
          })()}
    </div>
  )
}